.flex-container a:active,
.flexslider a:active,
.flex-container a:focus,
.flexslider a:focus  {outline: none;}
.slides,
.flex-control-nav,
.flex-direction-nav {margin: 0; padding: 0; list-style: none;}
.flex-pauseplay span {text-transform: capitalize;}

.slides:after {content: "."; display: block; clear: both; visibility: hidden; line-height: 0; height: 0;}
html[xmlns] .slides {display: block;}
* html .slides {height: 1%;}

.flex-viewport {max-height: 2000px; -webkit-transition: all 1s ease; -moz-transition: all 1s ease; transition: all 1s ease;}
.loading .flex-viewport {max-height: 300px;}
.carousel li {margin-right: 5px}
.slides li img {width:100%;}

/******************************************************************
SLIDER MODULE STYLES
******************************************************************/
.cb-slider-block { position: relative; zoom: 1; margin: 0;
    .cb-flex { position: relative; }
    .slides { zoom: 1; 
        li { margin-right:3px;  position: relative;  background:$blackish; overflow:hidden; display: none;
            img { vertical-align: top; }
        }
    }
   
    .flex-direction-nav { line-height: 0; text-align: left;
        a { z-index:10;  top: 0;opacity:0.7;  height: 100%; margin: 0; display: block;  position: absolute; background: #f5f5f5; width: 40px; padding: 0!important;
            i { top:50%; position:absolute; color: #4d4d4d;  -webkit-transform: translateY(-50%); -ms-transform: translateY(-50%); transform: translateY(-50%); width: 100%; text-align: center; font-size: 36px;     left: 0; }
        }
        .flex-next { right: 0; }
        .flex-prev { left: 0; }
        .flex-disabled { opacity: .3!important; filter:alpha(opacity=30); cursor: default; }
    }

}

.flexslider-1-fw { width:100%;  }

.cb-slider-a {
    .cb-flex h2 { font-size: 16px; margin-bottom: 10px; }
}

.cb-slider-a.cb-module-fw.cb-no-title { padding: 0 30px; background: none; }

.cb-slider-b {
    .slides {
        li { margin-right:0; }
    }
    .cb-review-ext-box { display:none; }
}

.flexslider-2.cb-featured {

    .slides li { height:520px; overflow:hidden;}
}

.comment-form-social-connect label { color: rgb(163, 163, 163);text-transform: uppercase; font-size: 13px; font-weight: 700; letter-spacing: 1px; float: none!important; text-align: center; }
#respond .comment-form-social-connect label { text-align: left; }
#respond .social_connect_ui p {margin-bottom: 10px; }

/******************************************************************
CSS ANIMATION STYLES
******************************************************************/
.cb-zero-trigger { width:0!important; }
.cb-zero-stars-trigger { width:100%!important; }

.cb-bar-ani {
  animation: cb-bar-ani 2s;
  -webkit-animation: cb-bar-ani 2s;
}
@keyframes cb-bar-ani {
   from { width: 0; }
}

@-webkit-keyframes cb-bar-ani {
   from { width: 0; }
}

@-moz-keyframes cb-bar-ani {
   from { width: 0; }
}

.cb-bar-ani-stars {
  animation: cb-bar-ani-stars 2s;
  -webkit-animation: cb-bar-ani-stars 2s;
}
@keyframes cb-bar-ani-stars {
   from { width: 100%; }
}

@-webkit-keyframes cb-bar-ani-stars {
   from { width: 100%; }
}
@-moz-keyframes cb-bar-ani-stars {
   from { width: 100%; }
}

.icon-facebook, .icon-twitter, .icon-youtube, .icon-instagram, .icon-linkedin, .icon-tumblr, .icon-flickr, .icon-pinterest {
    width: auto;
    height: auto;
    line-height: normal;
    vertical-align: baseline;
    background-image: none;
    background-position: 0% 0%;
    background-repeat: repeat;
    margin-top: 0;
    font-family: FontAwesome;
    font-weight: normal;
    font-style: normal;
    text-decoration: inherit;
    -webkit-font-smoothing: antialiased;
    &:before { text-decoration: inherit; display: inline-block; speak: none; }
}

.icon-facebook:before { content: "\f09a"; }

.icon-google-plus:before {
content: "\f0d5";
}

.icon-twitter:before {
content: "\f099";
}

.icon-instagram:before {
content: "\f16d";
}

.icon-linkedin:before {
content: "\f0e1";
}

.icon-pinterest:before {
  content: "\F0D2"
}

.icon-youtube:before {
content: "\f167";
}
.icon-tumblr:before {
 content: "\f173";
}

.icon-flickr:before {
 content: "\F16E";
}

.icon-2x {
font-size: 2em;
}

.fa-eye:before {
position: relative;
top: -1px;
}


.cb-pre-load .cb-recent ul { opacity: 1; @include transition(0.25s opacity ease); }
.cb-pre-load .cb-recent:before { -webkit-transform: translateY(-50%); -ms-transform: translateY(-50%); transform: translateY(-50%); font-family: 'fontawesome'; content: '\f1ce'; -webkit-animation: cb-spinner 1s infinite;  animation: cb-spinner 1s infinite; position: absolute; top: 50%; left: 0; width: 100%; text-align: center; font-size: 40px; z-index: 1; opacity: 0; @include transition(0.25s opacity ease);  }

.cb-dark-menu .cb-pre-load .cb-recent:before {   color: #fff;  }
.cb-light-menu .cb-pre-load .cb-recent:before {   color: #161616;  }

.cb-pre-load.cb-pro-load .cb-recent:before {  opacity: 1;   }
.cb-pre-load.cb-pro-load .cb-recent ul { opacity: 0.15; }
@-moz-keyframes cb-spinner {
    from { -moz-transform: rotate(0deg); }
    to { -moz-transform: rotate(360deg); }
}
@-webkit-keyframes cb-spinner {
    from { -webkit-transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); }
}
@keyframes cb-spinner {
    from {transform:rotate(0deg);}
    to {transform:rotate(360deg);}
}

