/*********************
NAVIGATION STYLES
*********************/
.cb-login-search {width:20%; float:left; text-align:right; padding: 5px 0;
	a { color:$grey3; margin-right:20px;
		i {margin-right:5px;}
		&:hover {text-decoration:none;}
	}
}
#cb-top-menu { width:100%;

	.wrap { padding-right: 10px; }
	.cb-mob { width: 50%; }
	.cb-breaking-news { width: 100%;
		ul li { font-size:16px; 
			.cb-mask {  float: left; }
		}
		span {padding: 0 20px;
			i {margin-left: 10px;}
		}
	}

	a { font-size:11px; letter-spacing: 1px; text-transform: uppercase; border-bottom: 0;  color: $grey4; 
		&:hover {text-decoration:underline;}   
	}

	.cb-top-nav { width:50%; float:right; text-align: right; display:block; border: 0; margin:0; height: 40px;
		.fa-2x { top: 3px;   position: relative; }
		.cb-has-children > a:after {content: "\f067"; font-family:fontawesome; font-size: 8px; color:$grey3; padding-left: 5px; vertical-align: middle;  }
		li {     float: right;  display: table; position: relative; height: 100%; @include transition(all 0.35s); &:hover{background:$grey9; a { color:$grey-2;} }
			a { border-bottom: 0; padding:0 10px; height: 100%; display: table-cell;   vertical-align: middle;
				&:hover{text-decoration:none!important;}  
			}

			ul.sub-menu, ul.children { text-align: center;  margin-top: 0; position: absolute; display: none; z-index: 8999; background:$grey1; border-top: 1px dashed $grey2;
				li { width:100%; border-bottom: 1px dashed $grey2; position: relative;  @include transition(background 0.35s); padding:0;
					&:hover{ > a { color:$grey-2;} }
					a {border-right: 0; width:100%; display:block; color:$grey3; line-height:1.5; padding: 10px; }

					&:last-child {border-bottom:none;
						a {  border-bottom: 0;}
					}


				}
			}

			&:hover > ul { top: 100%; display: block; left: 0;
				li { 
					ul { display:none; } 
					&:hover > ul { left: 100%; display: block; top: 0; }
				}
			}

		}

	}
	.cb-small-menu-icons { display: none; }
}

#cb-top-menu.cb-hidden { display:none; }

.admin-bar.cb-stuck #cb-nav-bar .cb-nav-bar-wrap { top: 32px; }
.admin-bar.cb-stuck-hid #cb-nav-bar .cb-nav-bar-wrap { top: 32px;  }

.cb-no-admin-bar.admin-bar.cb-stuck #cb-nav-bar .cb-nav-bar-wrap { top: 0; }
.cb-no-admin-bar.admin-bar.cb-stuck-hid #cb-nav-bar .cb-nav-bar-wrap { top: 0; }

.cb-stuck-hid #cb-nav-bar .cb-nav-bar-wrap { position: fixed; transform: translate3d( 0, -100%,0 ); }
.cb-stuck-hid.cb-stuck #cb-nav-bar .cb-nav-bar-wrap { transform: translate3d( 0, 0,0 ); @include transition(transform 0.25s); }
#cb-nav-bar {
	.cb-nav-bar-wrap { top: 0; z-index: 100; position: relative;  }
	#cb-main-menu { display:block; padding: 0; background-color: inherit; height: inherit;
		.main-nav { border: 0;; position: relative; text-align: center;  height: inherit;
			li { @include transition(background, 0.35s); display:inline-block; vertical-align:top; font-size: 14px;  text-transform: uppercase; margin-right: -2px;
				> a { padding: 9px 7px; display:inline-block;  letter-spacing: 1px; }
				li { margin-right: 0; }

				.cb-big-menu { position: absolute;  left:0; width: 750px; overflow:hidden; display:none; z-index:900;  backface-visibility: hidden;

					.cb-articles { width:100%;  float:right;  padding: 0 0 20px 0;

					  	.cb-mega-title { padding:10px 0; font-size: 14px; letter-spacing:2px;  text-transform:uppercase; margin: 0;
				  			span { border-bottom:3px solid transparent; padding: 0 0 10px 0; display:inline-block; }
				  		}
				  		.cb-recent {  float:left;  width:50%; padding: 0 30px; position: relative;

				  			ul { margin: 0; text-align:left;

				  				li {  width:100%;  margin: 0 0 10px 0; display:inline-block;

				  					.cb-mask {  position:relative;  float:left; margin-right: 20px; display:none;
				  						.cb-review-ext-box { top:0; }
				  						&:hover img {opacity: 0.7;}
				  						a { display:block;
				  							img {@include transition(all 0.35s); }

				  						}
				  					}
				  					.cb-meta {
				  						.h4  { margin: 0 0 2px 0; text-transform: none; font-size:13px; &:hover, &:focus { text-decoration: underline;  } }
				  						.cb-byline { display: none; font-size: 11px; color: $grey3; letter-spacing: 1px; margin:0; text-transform: none;
				  							a:hover {text-decoration:underline; }
				  						}
				  					}
				  				}
				  				li:last-child {margin: 0;}
				  			}
				  		}
				  		.cb-recent.cb-recent-fw {
				  			ul {
				  				li {width:48%; float:left;}
				  				.cb-article-1, .cb-article-3 { margin: 0 4% 15px 0; }
				  				.cb-article-5 { margin: 0 4% 0 0;}
				  				.cb-article-6 { margin: 0;}
				  			}
				  		}
				  		.cb-featured { width: 50%; float:left; padding: 0 20px 500px 20px; margin-bottom: -500px; 
							.cb-style-overlay img { opacity: 1;
								&:hover { opacity: 0.7; }
							}
				  		}
				  		.cb-slider-a { margin: 0 auto;
							.slides li { margin-right:3px; }
				  		}
				  		
					}
					.cb-articles.cb-with-sub { width:80%; }

					.cb-sub-menu { width:20%;  border-right: 1px solid $grey8; margin:0 0 -500px 0; padding-bottom: 500px; float:left;
						li { width:100%; border-bottom: 1px solid $grey8; &:hover{background:$grey8;} position: relative;
							a {padding:5px 10px; letter-spacing:1px; width: 100%; }
							.cb-grandchild-menu { display:none; position:absolute; top:0; right:-200px; width:200px; z-index:16; border-left:1px dashed $grey-2; li:last-child { border-bottom: none!important; } }
						}
					}
				}

				.cb-mega-menu { position: absolute;  left:0; width: 750px; overflow:hidden; display:none; z-index:900; background:$grey9; backface-visibility: hidden; padding:10px 0 20px 0;
					.cb-sub-menu { margin:0;
						> li {padding: 0 20px 1000px 20px;  margin-bottom: -1000px; float: left;  }
						li { width:25%;

							a { display:inline-block; color: $grey-3; font-weight:700; border-bottom: 3px solid transparent; padding: 15px 0 10px 0; margin: 0 0 15px 0;  }
							.cb-grandchild-menu { margin:0; width: 100%;  z-index:15;
								li {width:100%; margin: 0; border:none; padding:5px 20px;
									a { text-transform: none;border:none; margin:0; font-size:12px; font-weight:400; padding: 0; @include transition(background 0.35s); 
										&:hover{text-decoration:underline;}
									}
								}
							}
						}
					}
				}
				.cb-links-menu { position: relative;  z-index:900;

					.cb-sub-menu {  min-height: 37px; position: absolute; left:0; margin:0; display:none; width: 200px;  border-top: 1px dashed $grey-2;
						li { width:200px; border-bottom: 1px solid $grey8;   position: relative;  @include transition(background 0.35s);
							&:hover{background:$grey8;}
							a {padding:8px 12px; width: 100%;}
							.cb-grandchild-menu { z-index:15; display:none; position:absolute; top:0; right:-180px; width:180px; margin:0;  border-left:1px dashed $grey-2;}
						}
						li:last-child {border-bottom:none;}
					}
				}
			}

			.cb-icons { float:right; margin-right:0 ;
				.cb-menu-icon { float:right; position: relative; top: 50%; -webkit-transform: translateY(-50%); -ms-transform: translateY(-50%); transform: translateY(-50%); 

					a { display:block; padding: 0 8px; margin-right: 10px;
						i { font-size:16px; }
					}
				}
				.cb-icon-logged-in {   line-height: 1; 
					a img { height: 25px; width: 25px; }
				}
			}
			.cb-has-children > a:after {content: "\f067"; font-family:fontawesome; font-size: 10px; padding-left: 5px; vertical-align: middle;  }
			.cb-mega-menu .cb-has-children a:after {content: "";  }
			
		}
		#cb-nav-logo { display: table; height: 100%; 
			a { display: table-cell; vertical-align: middle; }
		}
	}
	.flexslider-1-menu { width: 560px; }
	.flexslider-1-fw-menu { width: 710px }
}

#cb-nav-bar.cb-dark-menu { background-color: $grey9;  background-color:$grey9t;
	#cb-main-menu {
		.main-nav {
			li {
				.cb-big-menu { background:$grey9;
					.cb-articles {
						.cb-mega-title span { color:$grey-3; }
						.cb-featured { border-right: 1px solid $grey8; }
						.cb-recent { 
							ul {
								li {
									.cb-meta {
										.cb-byline { color: $grey4;
											a { color: $grey4; }
										}
									}
								}
							}
						}
					}
					.cb-sub-menu { border-right: 1px solid $grey8; background:$grey9;
						li {border-bottom: 1px dashed $grey-4;
							&:hover{background:$grey8!important;}
						}
					}
				}
			 	.cb-links-menu {
				  	ul {background-color:$grey9;
				  		li { border-bottom: 1px dashed $grey-3;
				  			&:hover{background:$grey8!important;}
				  		}
				  	}
			 	}
				.cb-mega-menu {   background-color:$grey9;
					.cb-sub-menu { background:$grey9!important;
						li { background:$grey9!important;
							a { color: $grey-3;}
							.cb-grandchild-menu { background:$grey9!important; li { a { color: $grey2; } } }
						}
					}
				}
				&:hover > a {color:#fff;}
				a { color: $grey-1;	}
			}
			.current-menu-item a, .current_page_item a, .current-page-ancestor a, .current-category-ancestor a, .current-menu-ancestor a, .current-menu-parent a, .current-post-parent a, .current-post-ancestor a {color:$grey-4;}
			
		}
	}
}

