GALLERY POST STYLES
*****************************************************************/

#cb-gallery-post {

	#cb-gallery {
		.slides { 
			li { 
				.cb-meta {
					.cb-caption { padding:15px 30px; }
				}
			}
		}
		li {

			i {	color: #161616; background: #fff; background: rgba(255, 255, 255, 0.7); height: 60px; width: 60px; position: absolute; top: 50%; left: 50%; text-align: center; line-height: 60px; border-radius: 50% 50%; margin-left: -30px; margin-top: -30px; font-size: 20px; @include transition(opacity 0.35s); }
			img { @include transition(opacity 0.35s); }
			:hover {
				i { opacity: 1;}
				img { opacity: 0.7; }
			}
		}
	}

	#cb-carousel {
		.slides{
			li {
				img:hover {cursor:pointer;}
			}
		}

	}

